            <div v-for="(v, o) in categoryList" :key="o" class="secondMenu" :class="{ 'active': active == o }" @click="active = o">
                {{ v }}</div>
        </div>
        <div class="flexBox mt24" style="flex-wrap:wrap;">
            <div v-for="(v, o) in list" :key="o" class="newsList" @click="toDetail(v)">
                <div class="f500 text_deal_2">{{ v.title }}</div>
                <div class="f12 op7 mt4 text_deal">
                    <span>{{ v.resourceTime }}</span>·<span>{{ v.source }}</span>
                </div>
            </div>
        </div> -->
