        </div>
</div>
</template>
    
<script>

import axios from 'axios'
export default {
    name: '',
    props: {
    },
    components: {

    },
    data() {
        return {
            categoryList: ['全部', '重要舆情', '碳圈热点', '政府公告', '政府指标', '企业招标', '公众号'],
            active: 0,
            list: [],
            page: {
                "type": '',
                "current": 1,
                "size": 20,
                loading: false,
                finish: false,
            },
        }
    },
    mounted() {
        this.getList();
        this.getType();
    },
    methods: {
        getType() {
            this.$api.news_typs().then(res => {
            });
        },
        toDetail(v) {
            localStorage.setItem('information_detail', JSON.stringify(v));
            this.$router.push(`/informationDetail`);
        },
        getList2() {
            let d = {
                api_name: 'major_news', //接口名称
                token: 'f2f081163bfbe90486a61dff93ea0b9da84a1aff3aa1ebff8cd7ecb2', //用户的标识
                // token: 'b18690ef6f179d7adee66a3f9440d1951d819a6e8bd1d3add7255a80', //用户的标识
                params: {
                    src: '',
                    start_date: this.$dayjs(new Date()).format('YYYY-MM-DD 00:00:00'),
                    // start_date: this.$dayjs(new Date()).format('YYYY-MM-DD HH:mm:ss'),
                    // end_date:''
                },
                fields: 'title,content,pub_time,src',
            }
            axios.post(window.$config.baseUrl, d).then(response => {
                let res = response.data;
                if (res.code === 0) {
                    this.list = res.data.items;
                }
            });
        },
        //根据接口获取
        async getList() {
            if (this.page.loading) {
                return;
            }
            let d = {
                "type": this.page.type,
                "current": this.page.current,
                "size": this.page.size,
            };
            this.page.loading = true;
            let res = await this.$api.news_page(d);
            let list = res.list || [];
            let arr = [];
            list.forEach(item => {
                arr.push({
                    time: item.date,
                    close: item.close,
                    open: item.open,
                    high: item.highest,
                    low: item.lowest,
                    volume: item.totalAmount,
                })
            })
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if (d.current == 1) {
                this.bars = arr;
            } else {
                this.bars = arr.concat(this.bars);
            }
            this.page.loading = false;
        },
        // getList(){
        //     let d={
        //         api_name: 'stock_basic', //接口名称
        //         token: 'f2f081163bfbe90486a61dff93ea0b9da84a1aff3aa1ebff8cd7ecb2', //用户的标识
        //         params: {
        //             exchange: 'SSE',
        //             list_status: 'L'
        //         }, //接口参数，如daily接口中start_date和end_date
        //         fields: '',
        //     }
        //     axios.post(window.$config.baseUrl,d).then(res=>{

        //     });
        // }
    },
}
</script>
    
